<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup prescription__tem__pop box__shadow__02">
      <div class="popup__top">
        <h4>템플릿 저장</h4>
        <button class="pop__close__btn" @click="templateSavePopClose"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">템플릿명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                :disabled="popupState === '수정'"
                v-model="name"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select name="" id="" v-model="useYn">
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
        <li class="des__list">
          <dl>
            <dt class="f__medium">설명</dt>
            <dd>
              <textarea
                name=""
                id=""
                class="custom__input"
                v-model="description"
              ></textarea>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap" @click="saveBtnClick">
        <button class="custom__btn">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    popupEventFlag: String,
    popupState: String,
    prescriptionListByWeek: Array,
  },
  data() {
    return {
      name: "",
      description: "",
      status: "추가",
      useYn: "Y",
    };
  },
  watch: {
    // popupState() {
    //   if (this.popupState === "수정") {
    //   } else if (this.popupState === "등록") {
    //   }
    // },
    // prescriptionListByWeek() {
    // },
  },
  methods: {
    templateSavePopClose() {
      this.$emit("templateSavePopClose", "Close");
    },
    saveBtnClick() {
      const obj = {};
      obj.subjectId = "APP001";
      obj.name = this.name;
      obj.description = this.description;
      obj.useYn = this.useYn;
      let prescriptionListByWeek = [];
      for (let i = 0; i < 12; i++) {
        let treatmentCheck = 0;
        let treatmentList = [];
        for (let j = 0; j < 7; j++) {
          if (this.prescriptionListByWeek[i].treatmentList[j].id !== "") {
            treatmentCheck++;
            treatmentList.push(this.prescriptionListByWeek[i].treatmentList[j]);
          }
        }
        if (treatmentCheck >= 1) {
          prescriptionListByWeek.push({
            treatmentList: treatmentList,
            weekNum: i + 1,
          });
        }
      }
      obj.prescriptionListByWeek = prescriptionListByWeek;

      if (this.$route.params.pTemplateId !== undefined) {
        //
      } else {
        //
      }
    },
  },
  mounted() {
    if (this.$route.params.pTemplateId !== undefined) {
      this.status = "수정";
      this.name = this.$route.params.name;
      this.description = this.$route.params.description;
      this.useYn = this.$route.params.useYn;
    }
  },
};
</script>
